"use client";
import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import clsx from "clsx";

// palette for subgroup colors
const subgroupColors = [
  "bg-rose-100 text-rose-800",
  "bg-amber-100 text-amber-800",
  "bg-cyan-100 text-cyan-800",
  "bg-emerald-100 text-emerald-800",
];

export default function CategoryCard({
  id,
  catalog,
}: {
  id: string;
  catalog: any;
}) {
  const [selectedProblems, setSelectedProblems] = useState<string[]>([]);
  const [speed, setSpeed] = useState("");
  const [notes, setNotes] = useState("");

  // Available types under category
  const types = catalog?.[id] ? Object.keys(catalog[id]) : [];

  // Currently we take first type (expandable later)
  const type = types[0] || null;
  const problems = type ? catalog[id][type].problems || [] : [];

  const toggleProblem = (p: string) =>
    setSelectedProblems((curr) =>
      curr.includes(p) ? curr.filter((x) => x !== p) : [...curr, p]
    );

  // Simple heuristics to group problems
  const groups = [
    {
      name: "Word-related",
      items: problems.filter((p: string) =>
        /sign|word|symbol/i.test(p)
      ),
    },
    {
      name: "Placement-related",
      items: problems.filter((p: string) =>
        /height|spacing|placement|obstruct/i.test(p)
      ),
    },
    {
      name: "Visibility/Retro",
      items: problems.filter((p: string) =>
        /faded|retro|visibility/i.test(p)
      ),
    },
    {
      name: "Other",
      items: problems.filter(
        (p: string) =>
          !/sign|word|symbol|height|spacing|placement|obstruct|faded|retro|visibility/i.test(
            p
          )
      ),
    },
  ];

  return (
    <Card className="border">
      <CardHeader>
        <CardTitle className="text-lg">
          {id} — {type || "Select type"}
        </CardTitle>
      </CardHeader>

      <CardContent className="space-y-4">
        {/* PROBLEM GROUPS */}
        {groups.map(
          (g, gi) =>
            g.items.length > 0 && (
              <div key={g.name}>
                <div className="text-sm font-medium mb-2">{g.name}</div>

                <div className="flex flex-wrap gap-2">
                  {g.items.map((p: string) => (
                    <button
                      key={p}
                      onClick={() => toggleProblem(p)}
                      className={clsx(
                        "px-3 py-1 rounded-full text-sm border",
                        selectedProblems.includes(p)
                          ? `${subgroupColors[gi]} border-transparent shadow-sm`
                          : "bg-white dark:bg-zinc-800 border-zinc-200"
                      )}
                    >
                      {p}
                    </button>
                  ))}
                </div>
              </div>
            )
        )}

        {/* SPEED + NOTES */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-3">
          <input
            value={speed}
            onChange={(e) => setSpeed(e.target.value)}
            placeholder="Speed (e.g., 60 km/h)"
            className="p-2 border rounded"
          />

          <input
            value={notes}
            onChange={(e) => setNotes(e.target.value)}
            placeholder="Short notes (optional)"
            className="p-2 border rounded"
          />

          <div className="flex items-center gap-2">
            <button className="px-3 py-2 bg-zinc-100 rounded">Add</button>
          </div>
        </div>
      </CardContent>
    </Card>
  );
}
